/* ------------------------ utility functions ------------------------ */
function cn(...classes: Array<string | false | undefined | null>) {
  return classes.filter(Boolean).join(" ");
}

function generateId() {
  try {
    if (typeof crypto !== "undefined" && crypto.randomUUID) {
      return crypto.randomUUID();
    }
  } catch {}
  return Math.random().toString(36).slice(2);
}

function compareTime(timeA: string, timeB: string) {
  const toMinutes = (time: string) => {
    const [hours, minutes] = time.split(":").map((x) => parseInt(x, 10));
    return (hours || 0) * 60 + (minutes || 0);
  };
  return toMinutes(timeA) - toMinutes(timeB);
}

/* ------------------------ types and constants ------------------------ */
type DayKey =
  | "monday"
  | "tuesday"
  | "wednesday"
  | "thursday"
  | "friday"
  | "saturday"
  | "sunday";

interface WorkingHours {
  enabled: boolean;
  start: string;
  end: string;
}

interface Schedule {
  [key: string]: WorkingHours;
}

interface CalendarEvent {
  id: string;
  title: string;
  start: string;
  end: string;
  backgroundColor?: string;
  borderColor?: string;
  textColor?: string;
  extendedProps?: {
    type: "work" | "break" | "appointment";
    staffId: string;
  };
}

interface Staff {
  id: string;
  name: string;
  role: string;
  avatar: string;
  phone?: string;
  email?: string;
  skills?: string[];
  primarySkill?: string;
  notes?: string;
  schedule?: Schedule;
  color?: string;
}

