/* ------------------------ validation schemas ------------------------ */
const personalInfoSchema = z.object({
  firstName: z.string().min(1, "First name is required"),
  lastName: z.string().min(1, "Last name is required"),
  phone: z.string().optional(),
  email: z
    .string()
    .email("Please enter a valid email")
    .optional()
    .or(z.literal("")),
});

const specialtiesSchema = z.object({
  skills: z.array(z.string()).min(1, "Please select at least one specialty"),
  primarySkill: z.string().min(1, "Please select a primary specialty"),
  notes: z
    .string()
    .max(500, "Notes cannot exceed 500 characters")
    .optional()
    .or(z.literal("")),
});

const workingHoursSchema = z.object({
  enabled: z.boolean(),
  start: z.string(),
  end: z.string(),
});

const scheduleSchema = z
  .record(z.string(), workingHoursSchema)
  .superRefine((schedule, ctx) => {
    Object.entries(schedule).forEach(([day, hours]) => {
      if (hours.enabled) {
        if (!hours.start) {
          ctx.addIssue({
            code: z.ZodIssueCode.custom,
            path: [day, "start"],
            message: "Start time is required",
          });
        }
        if (!hours.end) {
          ctx.addIssue({
            code: z.ZodIssueCode.custom,
            path: [day, "end"],
            message: "End time is required",
          });
        }
        if (
          hours.start &&
          hours.end &&
          compareTime(hours.start, hours.end) >= 0
        ) {
          ctx.addIssue({
            code: z.ZodIssueCode.custom,
            path: [day, "end"],
            message: "End time must be after start time",
          });
        }
      }
    });
  });

/* ------------------------ main component ------------------------ */
export default function StaffManagementPage() {
  const [searchQuery, setSearchQuery] = React.useState("");
  const [staffList, setStaffList] = React.useState<Staff[]>(INITIAL_STAFF);
  const [activeStaffId, setActiveStaffId] = React.useState<string>(
    INITIAL_STAFF[0]?.id ?? ""
  );
  const [calendarEvents, setCalendarEvents] = React.useState<CalendarEvent[]>(
    []
  );

  const activeStaff = staffList.find((staff) => staff.id === activeStaffId);

  const filteredStaff = React.useMemo(() => {
    const query = searchQuery.toLowerCase().trim();
    if (!query) return staffList;

    return staffList.filter(
      (staff) =>
        staff.name.toLowerCase().includes(query) ||
        staff.role.toLowerCase().includes(query) ||
        staff.email?.toLowerCase().includes(query)
    );
  }, [searchQuery, staffList]);

  // Generate calendar events from staff schedules
  const generateCalendarEvents = React.useCallback(() => {
    const events: CalendarEvent[] = [];
    const today = new Date();
    const startOfWeek = new Date(today);
    startOfWeek.setDate(today.getDate() - today.getDay()); // Start from Sunday

    staffList.forEach((staff) => {
      if (!staff.schedule) return;

      // Generate events for next 4 weeks
      for (let week = 0; week < 4; week++) {
        Object.entries(staff.schedule).forEach(([dayName, hours]) => {
          if (!hours.enabled) return;

          const dayIndex = DAY_NAMES.indexOf(dayName as DayKey);
          if (dayIndex === -1) return;

          const eventDate = new Date(startOfWeek);
          eventDate.setDate(startOfWeek.getDate() + week * 7 + dayIndex + 1); // +1 because we start from Sunday

          const startDateTime = new Date(eventDate);
          const [startHour, startMin] = hours.start.split(":");
          startDateTime.setHours(parseInt(startHour), parseInt(startMin));

          const endDateTime = new Date(eventDate);
          const [endHour, endMin] = hours.end.split(":");
          endDateTime.setHours(parseInt(endHour), parseInt(endMin));

          events.push({
            id: `${staff.id}-${dayName}-${week}`,
            title: `${staff.name} - Available`,
            start: startDateTime.toISOString(),
            end: endDateTime.toISOString(),
            backgroundColor: staff.color || STAFF_COLORS[0],
            borderColor: staff.color || STAFF_COLORS[0],
            textColor: "#ffffff",
            extendedProps: {
              type: "work",
              staffId: staff.id,
            },
          });
        });
      }
    });

    setCalendarEvents(events);
  }, [staffList]);

  React.useEffect(() => {
    generateCalendarEvents();
  }, [generateCalendarEvents]);

  const addNewStaff = React.useCallback(() => {
    const colorIndex = staffList.length % STAFF_COLORS.length;
    const newStaff: Staff = {
      id: generateId(),
      name: "New Staff Member",
      role: "Staff Role",
      avatar:
        "https://ui-avatars.com/api/?name=New+Staff&background=137fec&color=fff",
      email: "",
      phone: "",
      skills: [],
      primarySkill: "",
      notes: "",
      schedule: createDefaultSchedule(),
      color: STAFF_COLORS[colorIndex],
    };

    setStaffList((prev) => [newStaff, ...prev]);
    setActiveStaffId(newStaff.id);
  }, [staffList.length]);

  const deleteStaff = React.useCallback(
    (staffId: string) => {
      setStaffList((prev) => prev.filter((staff) => staff.id !== staffId));

      if (staffId === activeStaffId) {
        const remainingStaff = staffList.find((staff) => staff.id !== staffId);
        setActiveStaffId(remainingStaff?.id ?? "");
      }
    },
    [activeStaffId, staffList]
  );

  /* ------------------------ forms ------------------------ */
  type PersonalInfoData = z.infer<typeof personalInfoSchema>;
  const personalInfoForm = useForm<PersonalInfoData>({
    resolver: zodResolver(personalInfoSchema),
    defaultValues: {
      firstName: "",
      lastName: "",
      phone: "",
      email: "",
    },
    mode: "onBlur",
  });

  type SpecialtiesData = z.infer<typeof specialtiesSchema>;
  const specialtiesForm = useForm<SpecialtiesData>({
    resolver: zodResolver(specialtiesSchema),
    defaultValues: {
      skills: [],
      primarySkill: "",
      notes: "",
    },
    mode: "onBlur",
  });

  type ScheduleData = z.infer<typeof scheduleSchema>;
  const scheduleForm = useForm<ScheduleData>({
    resolver: zodResolver(scheduleSchema),
    defaultValues: createDefaultSchedule(),
    mode: "onBlur",
  });

  // Reset forms when active staff changes
  React.useEffect(() => {
    if (!activeStaff) return;

    const [firstName, ...lastNameParts] = activeStaff.name.split(" ");
    const lastName = lastNameParts.join(" ");

    personalInfoForm.reset({
      firstName: firstName || "",
      lastName: lastName || "",
      phone: activeStaff.phone || "",
      email: activeStaff.email || "",
    });

    specialtiesForm.reset({
      skills: activeStaff.skills || [],
      primarySkill: activeStaff.primarySkill || "",
      notes: activeStaff.notes || "",
    });

    scheduleForm.reset(activeStaff.schedule || createDefaultSchedule());
  }, [
    activeStaffId,
    activeStaff,
    personalInfoForm,
    specialtiesForm,
    scheduleForm,
  ]);

  const savePersonalInfo = React.useCallback(
    (data: PersonalInfoData) => {
      if (!activeStaff) return;

      const fullName = `${data.firstName} ${data.lastName}`.trim();
      setStaffList((prev) =>
        prev.map((staff) =>
          staff.id === activeStaff.id
            ? {
                ...staff,
                name: fullName,
                phone: data.phone,
                email: data.email,
              }
            : staff
        )
      );
    },
    [activeStaff]
  );

  const saveSpecialties = React.useCallback(
    (data: SpecialtiesData) => {
      if (!activeStaff) return;

      setStaffList((prev) =>
        prev.map((staff) =>
          staff.id === activeStaff.id
            ? {
                ...staff,
                skills: data.skills,
                primarySkill: data.primarySkill,
                notes: data.notes,
              }
            : staff
        )
      );
    },
    [activeStaff]
  );

  const saveSchedule = React.useCallback(
    (data: ScheduleData) => {
      if (!activeStaff) return;

      setStaffList((prev) =>
        prev.map((staff) =>
          staff.id === activeStaff.id ? { ...staff, schedule: data } : staff
        )
      );
    },
    [activeStaff]
  );

  // Schedule quick actions
  const setAllDaysClosed = React.useCallback(() => {
    const currentSchedule = scheduleForm.getValues();
    const updatedSchedule: ScheduleData = { ...currentSchedule };

    DAY_NAMES.forEach((day) => {
      updatedSchedule[day] = { ...updatedSchedule[day], enabled: false };
    });

    scheduleForm.reset(updatedSchedule);
  }, [scheduleForm]);

  const setAllDaysNineToSix = React.useCallback(() => {
    const currentSchedule = scheduleForm.getValues();
    const updatedSchedule: ScheduleData = { ...currentSchedule };

    DAY_NAMES.forEach((day) => {
      updatedSchedule[day] = { enabled: true, start: "09:00", end: "18:00" };
    });

    scheduleForm.reset(updatedSchedule);
  }, [scheduleForm]);

  const copyMondayToAllDays = React.useCallback(() => {
    const currentSchedule = scheduleForm.getValues();
    const mondaySchedule = currentSchedule.monday;
    const updatedSchedule: ScheduleData = { ...currentSchedule };

    DAY_NAMES.forEach((day) => {
      updatedSchedule[day] = { ...mondaySchedule };
    });

    scheduleForm.reset(updatedSchedule);
  }, [scheduleForm]);

  return (
    <div className="p-8 max-w-7xl mx-auto">
      <div className="mb-6 flex flex-wrap items-center justify-between gap-4">
        <h1 className="text-3xl font-bold tracking-tight">Staff Management</h1>
      </div>

      <div className="grid grid-cols-1 gap-8 lg:grid-cols-4">
        {/* Staff List */}
        <div className="lg:col-span-1 flex flex-col gap-4">
          <div className="flex gap-2">
            <div className="relative flex-1 h-12">
              <Search className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 opacity-70" />
              <Input
                className="h-12 pl-9"
                placeholder="Search staff..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
              />
            </div>
            <Button className="h-12 px-4" onClick={addNewStaff}>
              <Plus className="h-4 w-4" />
            </Button>
          </div>

          <Card className="overflow-hidden">
            <CardContent className="p-0">
              <ScrollArea className="h-[600px]">
                <div className="divide-y">
                  {filteredStaff.map((staff) => {
                    const isSelected = staff.id === activeStaffId;
                    return (
                      <button
                        key={staff.id}
                        onClick={() => setActiveStaffId(staff.id)}
                        className={cn(
                          "w-full px-4 py-3 flex items-center justify-between gap-4 text-left transition-colors",
                          isSelected
                            ? "bg-primary/10"
                            : "hover:bg-muted/60 dark:hover:bg-muted/40"
                        )}
                      >
                        <div className="flex items-center gap-3">
                          <div
                            className="w-3 h-3 rounded-full"
                            style={{ backgroundColor: staff.color }}
                          />
                          <Avatar className="h-10 w-10">
                            <AvatarImage src={staff.avatar} alt={staff.name} />
                            <AvatarFallback>
                              {staff.name
                                .split(" ")
                                .map((part) => part[0])
                                .join("")
                                .slice(0, 2)
                                .toUpperCase()}
                            </AvatarFallback>
                          </Avatar>
                          <div className="min-w-0">
                            <p
                              className={cn(
                                "truncate text-sm font-medium",
                                isSelected && "text-primary"
                              )}
                            >
                              {staff.name}
                            </p>
                            <p className="truncate text-xs text-muted-foreground">
                              {staff.role}
                            </p>
                          </div>
                        </div>
                        <ChevronRight
                          className={cn(
                            "h-4 w-4",
                            isSelected
                              ? "text-primary"
                              : "text-muted-foreground"
                          )}
                        />
                      </button>
                    );
                  })}
                  {filteredStaff.length === 0 && (
                    <div className="px-4 py-6 text-sm text-muted-foreground">
                      No staff members found.
                    </div>
                  )}
                </div>
              </ScrollArea>
            </CardContent>
          </Card>
        </div>

        {/* Calendar & Staff Details */}
        <div className="lg:col-span-3 space-y-6">
          {/* Calendar View */}
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <CalendarIcon className="h-5 w-5" />
                Staff Schedule Calendar
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="mb-4 flex flex-wrap gap-2">
                {staffList.map((staff) => (
                  <Badge
                    key={staff.id}
                    variant="outline"
                    className="flex items-center gap-2"
                  >
                    <div
                      className="w-2 h-2 rounded-full"
                      style={{ backgroundColor: staff.color }}
                    />
                    {staff.name}
                  </Badge>
                ))}
              </div>

              <div className="bg-white rounded-lg border">
                <FullCalendar
                  plugins={[dayGridPlugin, timeGridPlugin, interactionPlugin]}
                  initialView="timeGridWeek"
                  headerToolbar={{
                    left: "prev,next today",
                    center: "title",
                    right: "dayGridMonth,timeGridWeek,timeGridDay",
                  }}
                  events={calendarEvents}
                  height="auto"
                  slotMinTime="07:00:00"
                  slotMaxTime="22:00:00"
                  allDaySlot={false}
                  weekends={true}
                  businessHours={{
                    daysOfWeek: [1, 2, 3, 4, 5, 6],
                    startTime: "09:00",
                    endTime: "18:00",
                  }}
                  eventClick={(info) => {
                    const staffId = info.event.extendedProps?.staffId;
                    if (staffId) {
                      setActiveStaffId(staffId);
                    }
                  }}
                  eventContent={(eventInfo) => (
                    <div className="p-1 text-xs">
                      <div className="font-medium">{eventInfo.event.title}</div>
                    </div>
                  )}
                />
              </div>
            </CardContent>
          </Card>

          {/* Staff Details */}
          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-xl">
                {activeStaff
                  ? `Edit ${activeStaff.name}`
                  : "Select a staff member"}
              </CardTitle>
            </CardHeader>
            <CardContent>
              {activeStaff ? (
                <Tabs defaultValue="personal">
                  <TabsList className="grid w-full grid-cols-3">
                    <TabsTrigger value="personal">Personal</TabsTrigger>
                    <TabsTrigger value="specialties">Skills</TabsTrigger>
                    <TabsTrigger value="schedule">Schedule</TabsTrigger>
                  </TabsList>

                  {/* Personal Information Tab */}
                  <TabsContent value="personal" className="mt-6 space-y-6">
                    <div className="flex items-center gap-6">
                      <Avatar className="h-20 w-20">
                        <AvatarImage
                          src={activeStaff.avatar}
                          alt={activeStaff.name}
                        />
                        <AvatarFallback>
                          {activeStaff.name
                            .split(" ")
                            .map((part) => part[0])
                            .join("")
                            .slice(0, 2)
                            .toUpperCase()}
                        </AvatarFallback>
                      </Avatar>

                      <div className="w-full">
                        <Label htmlFor="photo">Profile Photo</Label>
                        <Input id="photo" type="file" className="mt-2" />
                        <p className="mt-1 text-xs text-muted-foreground">
                          PNG, JPG, GIF (max. 800×800 px)
                        </p>
                      </div>
                    </div>

                    <Form {...personalInfoForm}>
                      <form
                        className="grid grid-cols-1 gap-4 md:grid-cols-2"
                        onSubmit={personalInfoForm.handleSubmit(
                          savePersonalInfo
                        )}
                      >
                        <FormField
                          control={personalInfoForm.control}
                          name="firstName"
                          render={({ field }) => (
                            <FormItem>
                              <FormLabel>First Name</FormLabel>
                              <FormControl>
                                <Input {...field} />
                              </FormControl>
                              <FormMessage />
                            </FormItem>
                          )}
                        />

                        <FormField
                          control={personalInfoForm.control}
                          name="lastName"
                          render={({ field }) => (
                            <FormItem>
                              <FormLabel>Last Name</FormLabel>
                              <FormControl>
                                <Input {...field} />
                              </FormControl>
                              <FormMessage />
                            </FormItem>
                          )}
                        />

                        <FormField
                          control={personalInfoForm.control}
                          name="phone"
                          render={({ field }) => (
                            <FormItem>
                              <FormLabel>Phone Number</FormLabel>
                              <FormControl>
                                <Input {...field} />
                              </FormControl>
                              <FormMessage />
                            </FormItem>
                          )}
                        />

                        <FormField
                          control={personalInfoForm.control}
                          name="email"
                          render={({ field }) => (
                            <FormItem>
                              <FormLabel>Email Address</FormLabel>
                              <FormControl>
                                <Input type="email" {...field} />
                              </FormControl>
                              <FormMessage />
                            </FormItem>
                          )}
                        />

                        <div className="md:col-span-2 flex justify-end gap-3 pt-2">
                          <Button
                            type="button"
                            variant="destructive"
                            onClick={() => deleteStaff(activeStaff.id)}
                          >
                            <Trash2 className="mr-2 h-4 w-4" />
                            Delete Staff
                          </Button>
                          <Button type="submit">Save Changes</Button>
                        </div>
                      </form>
                    </Form>
                  </TabsContent>

                  {/* Specialties Tab */}
                  <TabsContent value="specialties" className="mt-6">
                    <Form {...specialtiesForm}>
                      <form
                        className="space-y-6"
                        onSubmit={specialtiesForm.handleSubmit(saveSpecialties)}
                      >
                        <FormField
                          control={specialtiesForm.control}
                          name="skills"
                          render={({ field }) => (
                            <FormItem>
                              <FormLabel>Specialties</FormLabel>
                              <div className="mt-3 grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-3">
                                {SKILL_OPTIONS.map((skill) => {
                                  const isChecked =
                                    field.value?.includes(skill);
                                  return (
                                    <label
                                      key={skill}
                                      className={cn(
                                        "flex items-center gap-3 rounded-lg border p-3 cursor-pointer transition-colors",
                                        isChecked
                                          ? "border-primary bg-primary/5"
                                          : "border-border hover:bg-muted/50"
                                      )}
                                    >
                                      <Checkbox
                                        checked={isChecked}
                                        onCheckedChange={(checked) => {
                                          const updatedSkills = checked
                                            ? [...(field.value || []), skill]
                                            : (field.value || []).filter(
                                                (s) => s !== skill
                                              );
                                          field.onChange(updatedSkills);
                                        }}
                                      />
                                      <span className="text-sm">{skill}</span>
                                    </label>
                                  );
                                })}
                              </div>
                              <FormMessage />
                            </FormItem>
                          )}
                        />

                        <FormField
                          control={specialtiesForm.control}
                          name="primarySkill"
                          render={({ field }) => (
                            <FormItem>
                              <FormLabel>Primary Specialty</FormLabel>
                              <FormControl>
                                <Select
                                  value={field.value}
                                  onValueChange={field.onChange}
                                >
                                  <SelectTrigger>
                                    <SelectValue placeholder="Choose primary specialty" />
                                  </SelectTrigger>
                                  <SelectContent>
                                    {(
                                      specialtiesForm.getValues("skills") ||
                                      SKILL_OPTIONS
                                    ).map((skill) => (
                                      <SelectItem key={skill} value={skill}>
                                        {skill}
                                      </SelectItem>
                                    ))}
                                  </SelectContent>
                                </Select>
                              </FormControl>
                              <p className="text-xs text-muted-foreground">
                                Primary specialty will be highlighted in booking
                                system
                              </p>
                              <FormMessage />
                            </FormItem>
                          )}
                        />

                        <FormField
                          control={specialtiesForm.control}
                          name="notes"
                          render={({ field }) => (
                            <FormItem>
                              <FormLabel>Additional Notes</FormLabel>
                              <FormControl>
                                <Textarea
                                  className="min-h-[96px]"
                                  placeholder="Additional details, certifications, preferred products, etc."
                                  {...field}
                                />
                              </FormControl>
                              <FormMessage />
                            </FormItem>
                          )}
                        />

                        <div className="flex justify-end gap-3">
                          <Button
                            type="button"
                            variant="outline"
                            onClick={() => specialtiesForm.reset()}
                          >
                            Reset
                          </Button>
                          <Button type="submit">Save Specialties</Button>
                        </div>
                      </form>
                    </Form>
                  </TabsContent>

                  {/* Schedule Tab */}
                  <TabsContent value="schedule" className="mt-6">
                    <Form {...scheduleForm}>
                      <form
                        className="space-y-5"
                        onSubmit={scheduleForm.handleSubmit(saveSchedule)}
                      >
                        {/* Quick Actions */}
                        <div className="flex flex-wrap gap-2">
                          <Button
                            type="button"
                            variant="outline"
                            size="sm"
                            onClick={setAllDaysNineToSix}
                          >
                            Set All 09:00–18:00
                          </Button>
                          <Button
                            type="button"
                            variant="outline"
                            size="sm"
                            onClick={setAllDaysClosed}
                          >
                            Set All Closed
                          </Button>
                          <Button
                            type="button"
                            variant="outline"
                            size="sm"
                            onClick={copyMondayToAllDays}
                          >
                            Copy Monday to All
                          </Button>
                        </div>

                        {/* Schedule Grid */}
                        <div className="rounded-lg border">
                          <div className="grid grid-cols-12 items-center gap-2 border-b px-4 py-2 text-xs font-medium text-muted-foreground">
                            <div className="col-span-4">Day</div>
                            <div className="col-span-2">Open</div>
                            <div className="col-span-3">Start Time</div>
                            <div className="col-span-3">End Time</div>
                          </div>

                          {DAY_NAMES.map((day) => (
                            <div
                              key={day}
                              className="grid grid-cols-12 items-center gap-2 border-t px-4 py-3"
                            >
                              <div className="col-span-4 text-sm font-medium">
                                {DAY_LABELS[day]}
                              </div>

                              <div className="col-span-2">
                                <FormField
                                  control={scheduleForm.control}
                                  name={`${day}.enabled`}
                                  render={({ field }) => (
                                    <FormItem>
                                      <FormControl>
                                        <Switch
                                          checked={field.value}
                                          onCheckedChange={field.onChange}
                                        />
                                      </FormControl>
                                      <FormMessage />
                                    </FormItem>
                                  )}
                                />
                              </div>

                              <div className="col-span-3">
                                <FormField
                                  control={scheduleForm.control}
                                  name={`${day}.start`}
                                  render={({ field }) => (
                                    <FormItem>
                                      <FormControl>
                                        <Input
                                          type="time"
                                          disabled={
                                            !scheduleForm.watch(
                                              `${day}.enabled`
                                            )
                                          }
                                          {...field}
                                        />
                                      </FormControl>
                                      <FormMessage />
                                    </FormItem>
                                  )}
                                />
                              </div>

                              <div className="col-span-3">
                                <FormField
                                  control={scheduleForm.control}
                                  name={`${day}.end`}
                                  render={({ field }) => (
                                    <FormItem>
                                      <FormControl>
                                        <Input
                                          type="time"
                                          disabled={
                                            !scheduleForm.watch(
                                              `${day}.enabled`
                                            )
                                          }
                                          {...field}
                                        />
                                      </FormControl>
                                      <FormMessage />
                                    </FormItem>
                                  )}
                                />
                              </div>
                            </div>
                          ))}
                        </div>

                        <div className="flex justify-end gap-3">
                          <Button
                            type="button"
                            variant="outline"
                            onClick={() =>
                              scheduleForm.reset(
                                activeStaff?.schedule || createDefaultSchedule()
                              )
                            }
                          >
                            Reset
                          </Button>
                          <Button type="submit">Save Schedule</Button>
                        </div>
                      </form>
                    </Form>
                  </TabsContent>
                </Tabs>
              ) : (
                <p className="text-sm text-muted-foreground">
                  Please select a staff member from the list to view and edit
                  their details.
                </p>
              )}
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  );
}
